import React from "react";
import { Route, Switch, useRouteMatch } from "react-router-dom";

export const Antajat = () => {
  let match = useRouteMatch();
  return (
    <Switch>
      <Route path={`${match.path}/yritykset`}>
        <p>Pitää olla vähän yritystä edes</p>
      </Route>
      <Route path={`${match.path}/yrittajat`}>
        <p>Yrittää kannattaa aina! Paitsi just nyt</p>
      </Route>
      <Route path={match.path}>
        <p>Yrittääkö, vaiko eikö?</p>
      </Route>
    </Switch>
  );
};

export default Antajat;
