import React from "react";
import { Route, Switch, useRouteMatch } from "react-router-dom";
import Antajat from "./Antajat";
import Ottajat from "./Ottajat";

export const Tyo = () => {
  let match = useRouteMatch();
  return (
    <Switch>
      <Route path={`${match.path}/antajat`}>
        <Antajat />
      </Route>
      <Route path={`${match.path}/ottajat`}>
        <Ottajat />
      </Route>
      <Route path={match.path}>
        <p>Näiltä sivuilta löydät tietoa yrityksistä ja työnteon mahdollisuuksista.</p>
      </Route>
    </Switch>
  );
};

export default Tyo;
