import React from "react";
import { Route, Switch, useRouteMatch } from "react-router-dom";

export const Tervis = () => {
  let match = useRouteMatch();
  return (
    <Switch>
      <Route path={`${match.path}/hammas`}>
        <p>Hammasta kolottaa vai?</p>
      </Route>
      <Route path={`${match.path}/hoito`}>
        <p>Tarviitsii hoitoo terveyvelless</p>
      </Route>
      <Route path={match.path}>
        <p>Terveydenhoidosta päivää</p>
      </Route>
    </Switch>
  );
};

export default Tervis;
