import React from "react";
import { SearchInput as HDSSearch } from "hds-react";

export const SearchInput = () => {
  const [value, setValue] = React.useState('Coconut');
  type SuggestionItemType = {
    value: string;
  };
  const fruits = [
    'Apple',
    'Apricot',
    'Avocado',
    'Banana',
    'Blackberry',
    'Blueberry',
    'Cherry',
    'Coconut',
    'Cranberry',
    'Date',
    'Dragonfruit',
    'Fig',
    'Gooseberry',
    'Grape',
    'Honeyberry',
    'Jackfruit',
    'Kiwifruit',
    'Lemon',
    'Lime',
    'Mango',
    'Melon',
    'Nectarine',
    'Orange',
    'Papaya',
    'Passionfruit',
    'Peach',
    'Pear',
    'Pineapple',
    'Raspberry',
    'Satsuma',
    'Start fruit',
    'Strawberry',
    'Tangelo',
    'Tomato',
  ].map((fruit) => ({ value: fruit }));
  
  const asynchronousSearchOperation = (inputValue: string, timeout = 0) => {
    return new Promise<Array<{ value: string }>>((resolve) => {
      const filteredItems = fruits.filter((fruit) => {
        return fruit.value.toLowerCase().indexOf(inputValue.toLowerCase()) > -1;
      });
      setTimeout(() => {
        resolve(filteredItems);
      }, timeout);
    });
  };
  const onSubmit = (val: string) => {
    console.log(val)
  }
  const getSuggestions = async (inputValue: string): Promise<SuggestionItemType[]> => {
    const suggestions = await asynchronousSearchOperation(inputValue);
    return suggestions;
  };
  return (
    <div>
      <h1>SearchInput</h1>
      <div className="hds-search-input" style={{ maxWidth:320 }}>
        <HDSSearch
          label="Etsintä"
          suggestionLabelField="value"
          getSuggestions={getSuggestions}
          onSubmit={onSubmit}
          value={value}
          onChange={setValue}
        />
      </div>
    </div>
  );
};
