import React from "react";
import { Route, Switch, useRouteMatch } from "react-router-dom";
import { Linkbox } from "../linkbox";
import Senor from "./Senor";
import Tervis from "./Tervis";

export const Sossu = () => {
  let match = useRouteMatch();
  return (
    <Switch>
      <Route path={`${match.path}/tervis`}>
        <Tervis />
      </Route>
      <Route path={`${match.path}/senor`}>
        <Senor />
      </Route>
      <Route path={match.path}>
        <p>Sosiaali ja terveydenhoito sivu</p>
      </Route>
    </Switch>
  );
};

export default Sossu;
