import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "hds-core";
import "hds-design-tokens";

import "./App.css";
import {
  DateInput,
  SearchInput,
  TimeInput,
  Button,
  Tag,
  Header,
} from "./components";
import KasvuKoulu from "./components/pages/KasvuKoulu";
import Sossu from "./components/pages/Sossu";
import Tyo from "./components/pages/Tyo";

function App() {
  return (
    <Router>
      <div className="App">
        <Header />
        {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
        <Switch>
          <Route path="/sossu">
            <Sossu />
          </Route>
          <Route path="/kasvukoulu">
            <KasvuKoulu />
          </Route>
          <Route path="/tyo">
            <Tyo />
          </Route>
          <Route path="/">
            <p></p>Olet etusivulla! Tervetuloa!
            <DateInput />
            <SearchInput />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
