import React from "react";
import { Route, Switch, useRouteMatch } from "react-router-dom";

export const Ottajat = () => {
  let match = useRouteMatch();
  return (
    <Switch>
      <Route path={`${match.path}/avoimet`}>
        <p>Tässä on kaikki avoimet työpaikat:</p>
      </Route>
      <Route path={match.path}>
        <p>Ai töitä etit? Pitäis olla työkokemusta vähintään 5v ja mieluiten suoraan koulun penkiltä.</p>
      </Route>
    </Switch>
  );
};

export default Ottajat;
