import React, { useState } from "react";
import { DateInput as HDSDateInput, DateInputProps } from "hds-react";

export const DateInput = () => {
  const [pageIndex, setPageIndex] = useState(0);
  const [value, setValue] = useState<undefined | string>('5.11.2022');
  const [error, setError] = useState<undefined | string>(undefined);

  const handleChange = (val: string | undefined) => {
	console.log(val);
	if (val === '12') setError('Virhe');
	else if (val !== undefined && error !== undefined) setError(undefined)
	setValue(val)
  }

  const handleBlur = () => {
	console.log('blur');
  }
  return (
    <div>
      <h1>DateInput</h1>
		<HDSDateInput 
			disableConfirmation
			minDate={new Date(2022, 10, 1)}
			helperText="Use format D.M.YYYY"
			id="date-input-example-1"
			initialMonth={new Date()}
			label="Choose a date"
			language="en"
			onChange={handleChange}
			onBlur={handleBlur}
			required
			enterKeyHint={'Enter'}
			errorText={error}
			value={value}
		/>
	</div>
  );
};
