import React from "react";
import { Route, Switch, useRouteMatch } from "react-router-dom";

export const Senor = () => {
  let match = useRouteMatch();
  return (
    <Switch>
      <Route path={`${match.path}/virike`}>
        <p>Vanhuksille virikkeitä</p>
      </Route>
      <Route path={`${match.path}/kunts`}>
        <p>Kuntsiis kantsis kehittää. Et oo ennee nuor</p>
      </Route>
      <Route path={match.path}>
        <p>So you're a Senor!</p>
      </Route>
    </Switch>
  );
};

export default Senor;
