import React from "react";
import { Route, Switch, useRouteMatch } from "react-router-dom";

export const Koulutus = () => {
  let match = useRouteMatch();
  return (
    <Switch>
      <Route path={`${match.path}/perus`}>
        <p>Perusopetus kestää lapsen ikävuosista 7-16.</p>
      </Route>
      <Route path={`${match.path}/toinen`}>
        <p>Toisen asteen koulutusta on parinlaista! Amiskaa ja lukiota</p>
      </Route>
      <Route path={`${match.path}/opistot`}>
        <p>Entäs aikuisopistot? Siinä vasta paikka!</p>
      </Route>
      <Route path={match.path}>
        <p>Olet Koulutuksen sivuilla! Katso alemmat linkit.</p>
      </Route>
    </Switch>
  );
};

export default Koulutus;
