import React from "react";
import { Route, Switch, useRouteMatch } from "react-router-dom";
import Kasvatus from "./Kasvatus";
import Koulutus from "./Koulutus";

export const KasvuKoulu = () => {
  let match = useRouteMatch();
  return (
    <Switch>
      <Route path={`${match.path}/kasvatus`}>
        <Kasvatus />
      </Route>
      <Route path={`${match.path}/koulutus`}>
        <Koulutus />
      </Route>
      <Route path={match.path}>
        <p>Koulut pittää käyvä. Ja sitä ennen on kasvettava.</p>
      </Route>
    </Switch>
  );
};

export default KasvuKoulu;
