import React from "react";
import { Route, Switch, useRouteMatch } from "react-router-dom";

export const Kasvatus = () => {
  let match = useRouteMatch();
  return (
    <Switch>
      <Route path={`${match.path}/varhais`}>
        <p>Varhaisena on vitsa viännettävä. Ja pellot kiännettävä.</p>
      </Route>
      <Route path={`${match.path}/esi`}>
        <p>Jo esi-isämme muinaiset pirkkalaiset lähettivät lapsensa esiopetukseen pelloille.</p>
      </Route>
      <Route path={match.path}>
        <p>Olet kasvatus sivuilla!</p>
      </Route>
    </Switch>
  );
};

export default Kasvatus;
